/* eslint-disable react/prop-types */
import React, { useEffect, useState } from 'react';
// NOTE: Should we be upgrading to the v3 recaptcha?
// https://www.npmjs.com/package/react-google-recaptcha-v3

import ReCAPTCHA from 'react-google-recaptcha';
import { Box } from 'theme-ui';
import { ErrorText } from '@liveperson/lp-react-components';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';

const Recaptcha = ({ handleChange, error }) => {
  const recaptchaRef = React.createRef();
  const { i18n } = useTranslation();
  const [language, setLanguage] = useState(i18n.language);

  useEffect(() => {
    // Reloading the page is the only way to reset the recpatcha langauge
    if (language !== i18n.language) {
      setLanguage(i18n.language);
      window.location.reload();
    }
  }, [i18n.language, recaptchaRef, language]);

  return (
    <>
      <ReCAPTCHA
        ref={recaptchaRef}
        size="normal"
        sitekey={process.env.REACT_APP_RECAPTCHA_KEY}
        onChange={(value) => handleChange('recaptchaResponseToken', value)}
        hl={language}
      />
      <Box mt={2}>
        {error.isError && <ErrorText error={error} mb={3} />}
      </Box>
    </>
  );
};

Recaptcha.defaultProps = {
  error: {},
};

Recaptcha.propTypes = {
  handleChange: PropTypes.func.isRequired,
  error: PropTypes.shape({
    message: PropTypes.string,
    isError: PropTypes.bool,
  }),
};

export default Recaptcha;
